<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>营业额统计</p>
            </h3>
            <div class="searchInput">
                <div class="search_row">
                    <input class="my-search" v-model="value1" placeholder="输入人员/称谓查询" />
                    <i @click="submitHandler" class="icon-search"></i>
                    <div class="menuBox" align="right">
                        <i class="icon-menu" @click="openclick()"></i>
                    </div>
                </div>
            </div>
        </div>
        <div class="info_list">
            <pull-to @infinite-scroll="getMoreList" class="file-lists">
                <ul class="pull_ul">
                    <li v-for="item in dataList">
                        <p class="first">{{item.name}}<span>{{item.title}}</span></p>
                        <div class="second">
                            <span style="margin-right:1rem;">单&#12288;量：{{item.amount}}</span>
                            <span>营业额：{{item.settle_money|tofixTwo}}元</span>
                        </div>
                        <p class="third">时&#12288;间：{{item.times}}</p>
                        <div class="more on" @click="getNextList(item)">
                            <span class="icon-right"></span>
                        </div>
                    </li>
                </ul>
            </pull-to>
        </div>
        <div class="timeSearch" :class="{on:openbox}">
            <div class="timeSearch_inner">
                <p>开始时间：</p>
                <div class="startTime">
                    <yd-cell-item>
                        <yd-datetime type="date" v-model="startTime" slot="right"></yd-datetime>
                    </yd-cell-item>
                </div>
                <p>结束时间：</p>
                <div class="endTime">
                    <yd-cell-item>
                        <yd-datetime type="date" v-model="endTime" slot="right"></yd-datetime>
                    </yd-cell-item>
                </div>
            </div>
            <div class="searchBtn clearfixed">
                <button @click="submitHandler">搜  索</button>
            </div>
        </div>
    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                uid:"",
                count: 0,
                page: 1,
                page_number: 10,
                startTime: "2009-01-01",
                endTime: "2009-01-01",
                value1: "",
                openbox: false,
                dataList: [],
                historyCeng:[]
            }
        },
        methods: {
            openclick() {
                this.openbox = !this.openbox;
            },
            goLast() {
                if(this.historyCeng.length==0){
                    this.$router.go(-1);
                }else if(this.historyCeng.length==1){
                    this.uid = "";
                    this.getInfo();
                    this.historyCeng.length = this.historyCeng.length - 1;
                }
                else if(this.historyCeng.length>1){
                    this.uid = this.historyCeng[this.historyCeng.length-2].id;
                    this.getInfo();
                    this.historyCeng.length = this.historyCeng.length - 1;
                    console.log(this.historyCeng);
                }
            },
            submitHandler() {
                this.getInfo();
                this.openbox = false;
            },
            async getMoreList() {
                console.log("翻页");
                this.page = this.page + 1;
                let res = await ajax.Get(dataURL.main.lessMoney, {
                    page: this.page,
                    page_number: this.page_number,
                    keywords: this.value1,
                    start_date: this.startTime != "2009-01-01" ? this.startTime + " 00:00:00": "",
                    end_date: this.endTime != "2009-01-01" ? this.endTime + " 23:59:59": "",
                    uid:this.uid?this.uid:""
                })
                this.count = res.count;
                if (this.dataList.length >= res.count) {
                    this.page = this.page - 1;
                    this.$dialog.toast({
                        mes: "无更多数据",
                        timeout: 1500,
                        icon: "error"
                    });
                    return;
                }
                this.dataList = [...this.dataList, ...res.list];
                this.addTime();
            },
            getNextList(item) {
                let ceng = {};
                ceng.id = item.uid;
                this.historyCeng.push(ceng);
                console.log(this.historyCeng);
                this.uid = item.uid;
                this.getInfo();
            },
            async getInfo() {
                console.log(this.startTime);
                let res = await ajax.Get(dataURL.main.lessMoney, {
                    page: this.page,
                    page_number: this.page_number,
                    keywords: this.value1,
                    start_date: this.startTime != "2009-01-01" ? this.startTime + " 00:00:00": "",
                    end_date: this.endTime != "2009-01-01" ? this.endTime + " 23:59:59": "",
                    uid:this.uid?this.uid:""
                })
                this.dataList = res;
                this.addTime();
            },
            addTime(){
                this.dataList.forEach(element => {
                    let start = this.startTime != "2009-01-01" ? this.startTime : "";
                    let end = this.endTime != "2009-01-01" ? this.endTime : "";
                    let tos = start==""&&end==""?"":" 至 ";
                    element.times = start + tos + end;
                });
            }
        },
        filters: {
            tofixTwo: function (value) {
                return (parseFloat(value) / 100).toFixed(2);
            },
            tofixFour: function (value) {
                return (parseFloat(value) / 10000).toFixed(2);
            }
        },
        created() {
            this.historyCeng = [];
            this.getInfo();
        }
    }
</script>


